/*eslint-disable*/
import React from "react";
import * as Yup from "yup";
import { Formik } from "formik";
import axios from "axios";

import {
  Box,
  Button,
  FormHelperText,
  TextField,
  Stack,
} from "@material-ui/core";
import { Typography } from "@material-ui/core";
import userService from "../apis/userService";
import {customAxios} from "../apis/Axios";

const TransportForm = ({ setOpen,rows }) => {
  console.log("rows",rows)
  return (
    <Formik
      initialValues={{
        name: "",
        phone: "",
        license_no: "",
        vehicle_no: "",
        route_name: "",
      }}
      validationSchema={Yup.object().shape({
        name: Yup.string().required("Name is required"),
      })}
      onSubmit={async (values) => {
        try {
          await new Promise((r) => setTimeout(r, 500));
          
          customAxios.postCall("tranport/transport", {
              name: values.name,
              phone: values.phone,
              License_no: values.license_no,
              vehicle_no: values.vehicle_no,
              route_name: values.route_name,
            })
            .then((res) => {
              res.json;
            })
            .then((json) => {
              console.log(json);
            });
          setOpen(false);
        } catch (err) {
          console.log(err);
        }
      }}
    >
      {({
        errors,
        handleBlur,
        handleChange,
        handleSubmit,
        isSubmitting,
        touched,
        values,
      }) => (
        <form noValidate onSubmit={handleSubmit}>
          <Typography style={{ fontSize: 20, color: "black" }}>
            <strong>Add Transport</strong>
          </Typography>
          <Stack
            sx={{
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
              flexDirection: "column",
            }}
            spacing={2}
          >
            <TextField
              autoFocus
              error={Boolean(touched.name && errors.name)}
              fullWidth
              helperText={touched.name && errors.name}
              label="Name"
              margin="normal"
              id="name"
              onBlur={handleBlur}
              onChange={handleChange}
              value={values.name}
              variant="outlined"
            />
            <TextField
              fullwidth
              error={Boolean(touched.phone && errors.phone)}
              fullWidth
              helperText={touched.phone && errors.phone}
              label="Mobile"
              margin="normal"
              name="phone"
              onBlur={handleBlur}
              onChange={handleChange}
              type="phone"
              value={values.email}
              variant="outlined"
            />
            <TextField
              error={Boolean(touched.license_no && errors.license_no)}
              // sx={{ width: "50%" }}
              helperText={touched.license_no && errors.license_no}
              fullWidth
              id="license_no"
              label="LICENSE NO"
              margin="normal"
              onBlur={handleBlur}
              onChange={handleChange}
              type="text"
              value={values.license_no}
              variant="outlined"
            />{" "}
            <TextField
              error={Boolean(touched.vehicle_no && errors.vehicle_no)}
              fullWidth
              helperText={touched.vehicle_no && errors.vehicle_no}
              label="vehicle"
              margin="normal"
              id="vehicle_no"
              onBlur={handleBlur}
              onChange={handleChange}
              type="text"
              value={values.vehicle_no}
              variant="outlined"
            />
            <TextField
              error={Boolean(touched.route_name && errors.route_name)}
              fullWidth
              helperText={touched.route_name && errors.route_name}
              label="Route"
              margin="normal"
              id="route_name"
              onBlur={handleBlur}
              onChange={handleChange}
              type="text"
              value={values.route_name}
              variant="outlined"
            />
            {errors.submit && (
              <Box sx={{ mt: 3 }}>
                <FormHelperText error>{errors.submit}</FormHelperText>
              </Box>
            )}
            <Box sx={{ mt: 2 }}>
              <Button
                color="primary"
                disabled={isSubmitting}
                sx={{ width: "211px" }}
                size="large"
                type="submit"
                variant="contained"
                // onClick={edit}
              >
                Add Trasport
              </Button>
            </Box>
          </Stack>
        </form>
      )}
    </Formik>
  );
};
export default TransportForm;
