import React, { useState, useEffect } from "react";
import { Container ,Box} from "@material-ui/core";
import EnhancedTable from "../teachers/TeacherPagination";
import { customAxios } from "../apis/Axios";
import { Helmet } from "react-helmet-async";
import Header_Design from "../Transport/Header_Design"


const Student_list = () => {
  const [rows, setrows] = useState([]);
  const [refresh, setRefresh] = useState(true);

  const [open, setOpen] = React.useState(false);
  const teacher_data = async () => {
    const data = await customAxios.getCall(`teacher/teachers/api`);
    console.log(data);
    setrows(data);
  };

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };
  useEffect(() => {
    teacher_data();
  }, [refresh]);
  return (
    <>
      <Helmet>
        <title>Dashboard: Invoice List | Material Kit Pro</title>
      </Helmet>
      <Box
        sx={{
          backgroundColor: "background.default",
          minHeight: "100%",
          py: 8,
        }}
      >
        <Container>
          <Header_Design text="All Teacher Data" dashText="Staff Attendence" />
          <Box>
            <EnhancedTable
              invoices={rows}
              url="teacher/delete"
              refresh={refresh}
              setRefresh={setRefresh}
              navUrl="teacherform"
            />
          </Box>
        </Container>
      </Box>
    </>
  );
};
export default Student_list;
