import { useState } from "react";
import { Link as RouterLink, useNavigate } from "react-router-dom";
import { format } from "date-fns";
import PropTypes from "prop-types";
import {
  Avatar,
  Box,
  Button,
  Card,
  Checkbox,
  IconButton,
  InputAdornment,
  Link,
  Stack,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TablePagination,
  TableRow,
  TextField,
  Typography,
} from "@material-ui/core";
import SearchIcon from "../../icons/Search";
import ArrowRightIcon from "../../icons/ArrowRight";
import PencilAltIcon from "../../icons/PencilAlt";

// import Label from "../../../components/Label";
import Scrollbar from "../../components/Scrollbar";
import { customAxios } from "../apis/Axios";
import getInitials from "../../utils/getInitials";
import Label from "../../components/Label";



const getStatusLabel = (invoiceStatus) => {
  //   console.log("sta", invoiceStatus);
  const map = {
    notactive: {
      color: "error",
      text: "not active",
    },
    active: {
      color: "success",
      text: "active",
    },
  };

  const { text, color } = map[invoiceStatus];

  return <Label color={color}>{text}</Label>;
};

const applyFilters = (invoices, query, filters) =>
  invoices.filter((invoice) => {
    let matches = true;

    if (query) {
      const properties = ["first_name", "email"];
      let containsQuery = false;
      properties.forEach((property) => {
        const d = invoice[property];
        if (invoice[property].toLowerCase().includes(query.toLowerCase())) {
          containsQuery = true;
        }
      });

      if (!containsQuery) {
        matches = false;
      }
    }

    if (filters.status && invoice.status !== filters.status) {
      matches = false;
    }

    return matches;
  });

const applyPagination = (invoices, page, limit) =>
  invoices.slice(page * limit, page * limit + limit);

const TeacherPagination = (props) => {
  const { invoices, url, refresh, setfirst, navUrl, setRefresh, ...other } =
    props;
  const navigate = useNavigate();
  const [selectedInvoices, setSelectedInvoices] = useState([]);
  const [page, setPage] = useState(0);
  const [limit, setLimit] = useState(10);
  const [query, setQuery] = useState("");
  const [filters, setFilters] = useState({
    status: null,
  });

  const handleQueryChange = (event) => {
    setQuery(event.target.value);
  };



  const handleSelectAllInvoices = (event) => {
    // console.log("1",event.target.checked);
    setSelectedInvoices(
      event.target.checked ? invoices.map((invoice) => invoice.id) : []
    );
  };

  const removeHandler = async () => {
    try {
      if (confirm("Are Your Sure You Want To delete") == true) {
        let idString = "";
        selectedInvoices.forEach((ele) => {
          idString = ele + "," + idString;
        });
        idString = idString.slice(0, -1);

        const object = {
          id: idString,
        };
        // (object);

        await customAxios.putCall(`${url}`, { data: object }).then((res) => {
          res.json;
          setSelectedInvoices([]);
          setRefresh(!refresh);

          // ("res",res)
        });
      } else {
        setSelectedInvoices([...selectedInvoices]);
      }
    } catch (err) {
      console.log(err);
    }
    // console.log(Object.assign({},selectedInvoices));
    // console.log(selectedInvoices.reduce((a,v)=>({...a,[v]:v}),{}));
  };

  const handleSelectOneInvoice = (event, invoiceId) => {
    // console.log("2", invoiceId);
    if (!selectedInvoices.includes(invoiceId)) {
      setSelectedInvoices((prevSelected) => [...prevSelected, invoiceId]);
    } else {
      setSelectedInvoices((prevSelected) =>
        prevSelected.filter((id) => id !== invoiceId)
      );
    }
  };

  const handlePageChange = (event, newPage) => {
    setPage(newPage);
  };

  const handleLimitChange = (event) => {
    setLimit(parseInt(event.target.value, 10));
  };

  // Usually query is done on backend with indexing solutions
  const filteredInvoices = applyFilters(invoices, query, filters);
  const paginatedInvoices = applyPagination(filteredInvoices, page, limit);
  const enableBulkActions = selectedInvoices.length > 0;
  const selectedSomeInvoices =
    selectedInvoices.length > 0 && selectedInvoices.length < invoices.length;
  const selectedAllInvoices = selectedInvoices.length === invoices.length;

  return (
    <Card {...other}>
      <Box
        sx={{
          alignItems: "center",
          display: "flex",
          flexWrap: "wrap",
          m: -1,
          p: 2,
        }}
      >
        <Box
          sx={{
            m: 1,
            maxWidth: "100%",
            width: 500,
          }}
        >
          <TextField
            fullWidth
            InputProps={{
              startAdornment: (
                <InputAdornment position="end">
                  <SearchIcon fontSize="small" />
                </InputAdornment>
              ),
            }}
            onChange={handleQueryChange}
            placeholder="Search invoices by Student Name"
            value={query}
            variant="outlined"
          />
        </Box>
      </Box>
      {enableBulkActions && (
        <Box sx={{ position: "relative" }}>
          <Stack direction="row">
            <Box
              sx={{
                backgroundColor: "background.paper",
                mt: "6px",
                position: "absolute",
                px: "4px",
                width: "100%",
                zIndex: 2,
              }}
            >
              <Checkbox
                checked={selectedAllInvoices}
                color="primary"
                indeterminate={selectedSomeInvoices}
                onChange={handleSelectAllInvoices}
              />

              <Button
                color="primary"
                sx={{ ml: 2 }}
                variant="outlined"
                onClick={removeHandler}
              >
                Delete
              </Button>
              <Button color="primary" sx={{ ml: 2 }} variant="outlined">
                Edit
              </Button>
            </Box>
          </Stack>
        </Box>
      )}

      <Scrollbar>
        <Box sx={{ minWidth: 1200 }}>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell padding="checkbox">
                  <Checkbox
                    checked={selectedAllInvoices}
                    color="primary"
                    indeterminate={selectedSomeInvoices}
                    onChange={handleSelectAllInvoices}
                  />
                </TableCell>
                <TableCell>Name</TableCell>
                <TableCell>Role</TableCell>
                <TableCell>Phone</TableCell>
                <TableCell>Contract</TableCell>
                <TableCell>Action</TableCell>
                {/* <TableCell>Action</TableCell> */}
              </TableRow>
            </TableHead>
            <TableBody>
              {paginatedInvoices.map((row) => {
                const isInvoiceSelected = selectedInvoices.includes(row.id);
                // console.log("inv", isInvoiceSelected);
                // if (invoice.id == "") {
                //   {
                //     ("data not found");
                //   }
                // } else {
                return (
                  <TableRow hover key={row.id} selected={isInvoiceSelected}>
                    <TableCell padding="checkbox">
                      <Checkbox
                        checked={isInvoiceSelected}
                        color="primary"
                        onChange={(event) =>
                          handleSelectOneInvoice(event, row.id)
                        }
                        value={isInvoiceSelected}
                      />
                    </TableCell>
                    <TableCell>
                      <Box
                        sx={{
                          alignItems: "center",
                          display: "flex",
                        }}
                      >
                        <Avatar
                          src={`http://localhost:8000/uploads/${row.avatar}`}
                          sx={{
                            height: 42,
                            width: 42,
                          }}
                        >
                          {getInitials(row.first_name)}
                        </Avatar>
                        <Box sx={{ ml: 1 }}>
                          <Link
                            color="textPrimary"
                            component={RouterLink}
                            to="#"
                            underline="none"
                            variant="subtitle2"
                          >
                            {row.first_name}
                          </Link>
                          <Typography color="textSecondary" variant="body2">
                            {row.email}
                          </Typography>
                        </Box>
                      </Box>
                    </TableCell>
                    <TableCell>{row.Roles}</TableCell>
                    {/* <TableCell>{getStatusLabel(invoice.status)}</TableCell> */}

                    <TableCell>{row.phone}</TableCell>
                    <TableCell>{row.contract}</TableCell>

                    <TableCell align="left">
                      <Stack spacing={1} direction="row">
                        <Button
                          endIcon={<PencilAltIcon fontSize="small" />}
                          color="secondary"
                          variant="contained"
                          onClick={() =>
                            navigate(`/dashboard/${navUrl}`, {
                              state: { row },
                            })
                          }
                        >
                          Edit
                        </Button>
                        <Button
                          color="primary"
                          endIcon={<ArrowRightIcon fontSize="small" />}
                          variant="contained"
                          onClick={() =>
                            navigate("/dashboard/staff-detail", {
                              state: { row },
                            })
                          }
                        >
                          Detail
                        </Button>
                      </Stack>
                    </TableCell>

                    {/* <TableRow>
                      <IconButton
                        title="Edit Item"
                        component={RouterLink}
                        to="./TransportForm"
                        state={{ ...invoice }}
                      >
                        <ArrowRightIcon fontSize="small" />
                      </IconButton>
                    </TableRow> */}
                  </TableRow>
                );
                // }
              })}
            </TableBody>
          </Table>
        </Box>
      </Scrollbar>
      <TablePagination
        component="div"
        count={filteredInvoices.length}
        onPageChange={handlePageChange}
        onRowsPerPageChange={handleLimitChange}
        page={page}
        rowsPerPage={limit}
        rowsPerPageOptions={[5, 10, 25]}
      />
    </Card>
  );
};

TeacherPagination.propTypes = {
  invoices: PropTypes.array.isRequired,
};

export default TeacherPagination;
